@keyframes background-transition {
  0% {
    background-color: #ffffff;
  }

  17% {
    background-color: #C1C6C4;
  }

  34% {
    background-color: #7F9065;
  }

  51% {
    background-color: #FFE087;
  }

  68% {
    background-color: #9A87A4;
  }

  83% {
    background-color: #FFC9BE;
  }

  100% {
    background-color: #ffffff;
  }
}

html,
body {
  position: relative;
  height: 100%;
}

body {
  animation-name: background-transition;
  animation-duration: 15s;
  animation-iteration-count: infinite;
  transition: background-color 1000ms;
}

.about {
  position: absolute;
  width: 100vw;
  left: 0;
  top: 10px;
  text-align: center;
}

.footer {
  position: absolute;
  width: 100vw;
  left: 0;
  bottom: 15px;
  text-align: center;
}

.pee {
  position: absolute;
  left: 10px;
  top: calc(50vh - 60px);
  height: 120px;
}

.ess {
  position: absolute;
  right: 0px;
  top: calc(50vh - 60px);
  height: 120px;
}

.line {
  display: block;
  position: absolute;
  top: calc(50vh - 60px);
  transform: translateY(50px);
  width: calc(100vw - 320px);
  left: 150px;
  height: 15px;
  background-color: #141414;
}

